import { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { ArrowLeft, Send, AlertCircle, Image as ImageIcon } from 'lucide-react';
import { useCreatePost, useCategories } from '../hooks/usePosts';
import { getAnonymousId, getUsername, setUsername } from '../utils/anonymousUser';
import { showToast } from '../utils/toast';
import ImageUploader from '../components/post/ImageUploader';

// ============================================
//   
// ============================================

interface FormData {
  title: string;
  content: string;
  author: string;
  password: string;
  categoryId: number;
  instagram: string;
}

function validateForm(data: FormData, images: File[]): string | null {
  if (!data.title.trim()) return ' .';
  if (data.title.length > 200) return ' 200  .';
  
  if (!data.content.trim()) return ' .';
  if (data.content.length > 10000) return ' 10000  .';
  
  if (!data.author.trim()) return ' .';
  if (data.author.length > 50) return ' 50  .';
  
  if (!data.password) return ' .';
  if (data.password.length < 4) return ' 4  .';
  
  if (images.length > 5) return '  5   .';
  
  return null;
}

// ============================================
//  
// ============================================

export default function WritePage() {
  const navigate = useNavigate();
  const anonymousId = getAnonymousId();
  
  //  
  const [formData, setFormData] = useState<FormData>({
    title: '',
    content: '',
    author: '',
    password: '',
    categoryId: 2, // : 
    instagram: '',
  });
  const [images, setImages] = useState<File[]>([]);
  const [error, setError] = useState<string | null>(null);
  
  // API 
  const { data: categoriesData } = useCategories();
  const createPostMutation = useCreatePost();
  
  const categories = categoriesData || [];
  
  /**
   *      
   */
  useEffect(() => {
    const savedUsername = getUsername();
    if (savedUsername) {
      setFormData(prev => ({ ...prev, author: savedUsername }));
    }
  }, []);
  
  /**
   *   
   */
  const handleChange = (field: keyof FormData, value: string | number) => {
    setFormData(prev => ({ ...prev, [field]: value }));
    setError(null);
  };
  
  /**
   *   
   */
  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setError(null);
    
    //  
    const validationError = validateForm(formData, images);
    if (validationError) {
      setError(validationError);
      return;
    }
    
    try {
      //  
      setUsername(formData.author);
      
      //   
      const category = categories.find(c => c.id === formData.categoryId);
      const categoryName = category?.name || '';
      
      //  
      const post = await createPostMutation.mutateAsync({
        data: {
          title: formData.title.trim(),
          content: formData.content.trim(),
          author: formData.author.trim(),
          password: formData.password,
          category: categoryName,
          instagram: formData.instagram.trim() || undefined,
          anonymousId,
        },
        images,
      });
      
      showToast('  !', { type: 'success' });
      setTimeout(() => navigate(`/post/${post.id}`), 500);
    } catch (err: any) {
      console.error('  :', err);
      setError(err.message || '  .');
      showToast('  .', { type: 'error' });
    }
  };
  
  /**
   *  
   */
  const handleCancel = () => {
    if (formData.title || formData.content || images.length > 0) {
      if (!confirm('   .  ?')) {
        return;
      }
    }
    navigate(-1);
  };
  
  const isSubmitting = createPostMutation.isPending;
  
  return (
    <div className="min-h-screen bg-neutral-50 py-8 px-4">
      <div className="max-w-4xl mx-auto">
        {/*  */}
        <div className="mb-6 flex items-center justify-between">
          <button
            onClick={handleCancel}
            className="flex items-center gap-2 text-neutral-500 hover:text-neutral-900 transition-colors min-h-[44px]"
            disabled={isSubmitting}
          >
            <ArrowLeft className="w-5 h-5" />
            <span className="text-sm font-medium"></span>
          </button>
          
          <h1 className="text-2xl font-bold text-neutral-900">
             
          </h1>
          
          <div className="w-24" /> {/* Spacer */}
        </div>
        
        {/*   */}
        {error && (
          <div className="mb-6 p-4 bg-danger-50 border border-danger-200 rounded-xl flex items-start gap-3 animate-fadeIn">
            <AlertCircle className="w-5 h-5 text-danger-500 flex-shrink-0 mt-0.5" />
            <p className="text-danger-700 text-sm">{error}</p>
          </div>
        )}
        
        {/*   */}
        <form onSubmit={handleSubmit} className="space-y-6">
          {/*  &   */}
          <div className="card">
            <div className="card-body">
              <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                {/*  */}
                <div>
                  <label className="block text-sm font-medium text-neutral-700 mb-2">
                     <span className="text-danger-500">*</span>
                  </label>
                  <select
                    value={formData.categoryId}
                    onChange={(e) => handleChange('categoryId', Number(e.target.value))}
                    className="select"
                    disabled={isSubmitting}
                    required
                  >
                    {categories.length > 0 ? (
                      categories.map((cat) => (
                        <option key={cat.id} value={cat.id}>
                          {cat.icon} {cat.name}
                        </option>
                      ))
                    ) : (
                      <>
                        <option value={1}> </option>
                        <option value={2}> </option>
                        <option value={3}> </option>
                        <option value={4}> </option>
                        <option value={5}> </option>
                        <option value={6}> </option>
                      </>
                    )}
                  </select>
                </div>
                
                {/*  */}
                <div>
                  <label className="block text-sm font-medium text-neutral-700 mb-2">
                     <span className="text-danger-500">*</span>
                  </label>
                  <input
                    type="text"
                    value={formData.author}
                    onChange={(e) => handleChange('author', e.target.value)}
                    placeholder=""
                    maxLength={50}
                    className="input"
                    disabled={isSubmitting}
                    required
                  />
                </div>
                
                {/*  */}
                <div>
                  <label className="block text-sm font-medium text-neutral-700 mb-2">
                     <span className="text-danger-500">*</span>
                  </label>
                  <input
                    type="password"
                    value={formData.password}
                    onChange={(e) => handleChange('password', e.target.value)}
                    placeholder="4 "
                    minLength={4}
                    className="input"
                    disabled={isSubmitting}
                    required
                  />
                </div>
              </div>
              
              {/* Instagram () */}
              <div className="mt-4">
                <label className="block text-sm font-medium text-neutral-700 mb-2">
                  Instagram <span className="text-neutral-400">()</span>
                </label>
                <input
                  type="text"
                  value={formData.instagram}
                  onChange={(e) => handleChange('instagram', e.target.value)}
                  placeholder="@username"
                  className="input"
                  disabled={isSubmitting}
                />
              </div>
            </div>
          </div>
          
          {/*  */}
          <div className="card">
            <div className="card-body">
              <label className="block text-sm font-medium text-neutral-700 mb-2">
                 <span className="text-danger-500">*</span>
              </label>
              <input
                type="text"
                value={formData.title}
                onChange={(e) => handleChange('title', e.target.value)}
                placeholder="  ( 200)"
                maxLength={200}
                className="input text-lg"
                disabled={isSubmitting}
                required
              />
              <p className="mt-2 text-xs text-neutral-400 text-right">
                {formData.title.length} / 200
              </p>
            </div>
          </div>
          
          {/*  */}
          <div className="card">
            <div className="card-body">
              <label className="block text-sm font-medium text-neutral-700 mb-2">
                 <span className="text-danger-500">*</span>
              </label>
              <textarea
                value={formData.content}
                onChange={(e) => handleChange('content', e.target.value)}
                placeholder="  ( 10000)"
                maxLength={10000}
                rows={15}
                className="textarea"
                disabled={isSubmitting}
                required
              />
              <p className="mt-2 text-xs text-neutral-400 text-right">
                {formData.content.length} / 10000
              </p>
            </div>
          </div>
          
          {/*   */}
          <div className="card">
            <div className="card-body">
              <label className="block text-sm font-medium text-neutral-700 mb-4 flex items-center gap-2">
                <ImageIcon className="w-5 h-5 text-primary-500" />
                <span>  <span className="text-neutral-400">( 5)</span></span>
              </label>
              <ImageUploader
                images={images}
                onImagesChange={setImages}
                maxImages={5}
                maxSizeKB={5120}
              />
            </div>
          </div>
          
          {/*   */}
          <div className="flex flex-col sm:flex-row items-center justify-between gap-3">
            <button
              type="button"
              onClick={handleCancel}
              className="btn-secondary w-full sm:w-auto"
              disabled={isSubmitting}
            >
              
            </button>
            
            <button
              type="submit"
              disabled={isSubmitting}
              className="btn-primary w-full sm:w-auto"
            >
              {isSubmitting ? (
                <>
                  <div className="w-5 h-5 border-2 border-white border-t-transparent rounded-full animate-spin" />
                  <span> ...</span>
                </>
              ) : (
                <>
                  <Send className="w-5 h-5" />
                  <span> </span>
                </>
              )}
            </button>
          </div>
        </form>
        
        {/*   */}
        <div className="mt-8 card">
          <div className="card-body">
            <h3 className="text-sm font-bold text-neutral-900 mb-3 flex items-center gap-2">
              <AlertCircle className="w-4 h-4 text-primary-500" />
               
            </h3>
            <ul className="space-y-2 text-sm text-neutral-600">
              <li className="flex items-start gap-2">
                <span className="text-primary-500"></span>
                <span> Cloudinary CDN    .</span>
              </li>
              <li className="flex items-start gap-2">
                <span className="text-primary-500"></span>
                <span>   /  .</span>
              </li>
              <li className="flex items-start gap-2">
                <span className="text-primary-500"></span>
                <span>, ,        .</span>
              </li>
              <li className="flex items-start gap-2">
                <span className="text-primary-500"></span>
                <span>      .</span>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </div>
  );
}
