/**
 *   (v4.0.0 - Clean Architecture)
 * 
 *  API     
 */

// ============================================
// 
// ============================================

export interface Category {
  id: number;
  name: string;
  icon: string;
  color: string;
  description: string;
  sort_order: number;
  is_active: boolean;
}

// ============================================
// 
// ============================================

export interface PostImage {
  id: number;
  cloudinary_id?: string;
  cloudinary_url: string;
  thumbnail_url: string;
  original_filename?: string;
  file_size?: number;
  width: number;
  height: number;
  format?: string;
  sort_order?: number;
}

// ============================================
// 
// ============================================

export interface Comment {
  id: number;
  content: string;
  author: string;
  instagram?: string;
  created_at: string;
  is_blinded?: boolean;
}

// ============================================
// 
// ============================================

export interface Post {
  //  
  id: string;
  title: string;
  content: string;
  author: string;
  instagram?: string;
  
  // 
  category_id: number;
  category_name: string;
  category_icon: string;
  category_color: string;
  
  // 
  views: number;
  likes_count: number;
  dislikes_count: number;
  comments_count: number;
  
  //   
  images: PostImage[];
  comments?: Comment[];
  
  // 
  is_notice: boolean;
  is_pinned: boolean;
  is_blinded: boolean;
  
  // 
  created_at: string;
  updated_at: string;
  
  //  
  user_id: string;
  username?: string;
}

// ============================================
// API 
// ============================================

export interface CreatePostRequest {
  title: string;
  content: string;
  author: string;
  password: string;
  category: string;
  instagram?: string;
  anonymousId: string;
}

export interface CreateCommentRequest {
  content: string;
  author: string;
  instagram?: string;
  anonymousId: string;
}

export interface VoteRequest {
  type: 'like' | 'dislike';
  anonymousId: string;
}

export interface DeletePostRequest {
  password: string;
}

// ============================================
// API 
// ============================================

export interface ApiResponse<T> {
  success: boolean;
  data?: T;
  message?: string;
  error?: string;
}

export interface PostsResponse {
  success: boolean;
  posts: Post[];
  count: number;
  page?: number;
  limit?: number;
}

export interface PostDetailResponse {
  success: boolean;
  post: Post;
}

export interface HealthResponse {
  status: 'healthy' | 'unhealthy';
  version: string;
  database: 'connected' | 'disconnected';
  cloudinary: 'configured' | 'not_configured';
  websocket: string;
  timestamp: string;
}

// ============================================
// WebSocket
// ============================================

export interface WebSocketMessage {
  type: 'new_post' | 'new_comment' | 'vote' | 'post_deleted' | 'comment_deleted';
  data?: any;
  postId?: string;
  timestamp: string;
}

// ============================================
//  
// ============================================

export const STORAGE_KEYS = {
  ANONYMOUS_ID: 'athletetime_anonymous_id',
  USERNAME: 'athletetime_username',
  THEME: 'athletetime_theme',
  VOTED_POSTS: 'athletetime_voted_posts',
} as const;

// ============================================
//  
// ============================================

export type VoteType = 'like' | 'dislike';

export interface VotedPosts {
  [postId: string]: VoteType;
}
